"use client"

import { BorrowRequest } from "../lib/api"
import { useState } from "react"
import ApprovalDialog from "./ApprovalDialog"

interface RequestCardProps {
  request: BorrowRequest
  onApprove: (id: string, reason: string) => void
  onReject: (id: string, reason: string) => void
  approverName?: string
}

export default function RequestCard({ request, onApprove, onReject, approverName = "Approver" }: RequestCardProps) {
  const [showDialog, setShowDialog] = useState(false)

  const handleApproveWithReason = async (id: string, reason: string) => {
    await onApprove(id, reason)
  }

  const handleRejectWithReason = async (id: string, reason: string) => {
    await onReject(id, reason)
  }

  const getStatusBadge = (status: string) => {
    switch (status) {
      case 'pending':
        return (
          <span className="inline-flex items-center rounded-full bg-yellow-100 px-3 py-1 text-xs font-medium text-yellow-800 ring-1 ring-inset ring-yellow-600/20">
            Menunggu Persetujuan
          </span>
        )
      case 'approved':
        return (
          <span className="inline-flex items-center rounded-full bg-green-100 px-3 py-1 text-xs font-medium text-green-800 ring-1 ring-inset ring-green-600/20">
            Disetujui
          </span>
        )
      case 'rejected':
        return (
          <span className="inline-flex items-center rounded-full bg-red-100 px-3 py-1 text-xs font-medium text-red-800 ring-1 ring-inset ring-red-600/20">
            Ditolak
          </span>
        )
      default:
        return null
    }
  }

  const formatDate = (dateString: string) => {
    return new Date(dateString).toLocaleDateString('id-ID', {
      weekday: 'long',
      year: 'numeric',
      month: 'long',
      day: 'numeric'
    })
  }

  const formatCreatedAt = (dateString: string) => {
    return new Date(dateString).toLocaleDateString('id-ID', {
      day: 'numeric',
      month: 'short',
      year: 'numeric',
      hour: '2-digit',
      minute: '2-digit'
    })
  }

  return (
    <div className="bg-white border border-blue-200 rounded-xl p-6 shadow-lg hover:shadow-xl transition-shadow">
      <div className="flex items-start justify-between mb-4">
        <div>
          <h3 className="text-lg font-semibold text-blue-900">{request.name}</h3>
          <p className="text-sm text-blue-600">Diajukan pada {formatCreatedAt(request.createdAt)}</p>
        </div>
        {getStatusBadge(request.status)}
      </div>

      <div className="space-y-4 mb-6">
        <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
          <div className="bg-blue-50 p-3 rounded-lg border border-blue-100">
            <p className="text-sm font-medium text-blue-800">Barang/Kendaraan</p>
            <p className="text-sm text-blue-900 font-semibold">{request.item}</p>
          </div>
          <div className="bg-yellow-50 p-3 rounded-lg border border-yellow-100">
            <p className="text-sm font-medium text-blue-800">Tanggal Peminjaman</p>
            <p className="text-sm text-blue-900 font-semibold">{formatDate(request.date)}</p>
          </div>
        </div>
        
        <div>
          <p className="text-sm font-medium text-blue-800 mb-2">Keperluan Dinas</p>
          <p className="text-sm text-blue-900 bg-blue-50 p-3 rounded-lg border border-blue-100">{request.purpose}</p>
        </div>
      </div>

      {request.status === "pending" && (
        <div className="flex gap-3 pt-4 border-t border-blue-200">
          <button 
            onClick={() => setShowDialog(true)}
            className="flex-1 inline-flex items-center justify-center rounded-md text-sm font-medium transition-colors focus-visible:outline-none focus-visible:ring-2 focus-visible:ring-blue-400 focus-visible:ring-offset-2 bg-blue-600 text-white hover:bg-blue-700 h-10 px-4 py-2 shadow-md"
          >
            Review Permintaan
          </button>
        </div>
      )}

      {/* Show approval/rejection reason if available */}
      {request.status !== "pending" && request.reason && (
        <div className="mt-4 p-3 bg-gray-50 rounded-lg border border-gray-200">
          <p className="text-sm font-medium text-gray-700 mb-1">
            Alasan {request.status === "approved" ? "Persetujuan" : "Penolakan"}:
          </p>
          <p className="text-sm text-gray-600">{request.reason}</p>
          {request.approvedBy && request.approvedAt && (
            <p className="text-xs text-gray-500 mt-2">
              Oleh: {request.approvedBy} • {new Date(request.approvedAt).toLocaleDateString('id-ID', {
                day: 'numeric',
                month: 'short',
                year: 'numeric',
                hour: '2-digit',
                minute: '2-digit'
              })}
            </p>
          )}
        </div>
      )}

      {/* Approval Dialog */}
      <ApprovalDialog
        request={request}
        isOpen={showDialog}
        onClose={() => setShowDialog(false)}
        onApprove={handleApproveWithReason}
        onReject={handleRejectWithReason}
        approverName={approverName}
      />
    </div>
  )
}
