"use client"

import { useState } from "react"
import { BorrowRequest } from "../lib/api"

interface ApprovalDialogProps {
  request: BorrowRequest
  isOpen: boolean
  onClose: () => void
  onApprove: (id: string, reason: string) => void
  onReject: (id: string, reason: string) => void
  approverName: string
}

export default function ApprovalDialog({ 
  request, 
  isOpen, 
  onClose, 
  onApprove, 
  onReject, 
  approverName 
}: ApprovalDialogProps) {
  const [action, setAction] = useState<'approve' | 'reject' | null>(null)
  const [reason, setReason] = useState("")
  const [isSubmitting, setIsSubmitting] = useState(false)

  if (!isOpen) return null

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault()
    if (!action || !reason.trim()) return

    setIsSubmitting(true)
    try {
      if (action === 'approve') {
        await onApprove(request.id, reason.trim())
      } else {
        await onReject(request.id, reason.trim())
      }
      setReason("")
      setAction(null)
      onClose()
    } catch (error) {
      console.error('Error submitting approval:', error)
    } finally {
      setIsSubmitting(false)
    }
  }

  const formatDate = (dateString: string) => {
    return new Date(dateString).toLocaleDateString('id-ID', {
      weekday: 'long',
      year: 'numeric',
      month: 'long',
      day: 'numeric'
    })
  }

  return (
    <div className="fixed inset-0 bg-black bg-opacity-50 flex items-center justify-center z-50 p-4">
      <div className="bg-white rounded-xl shadow-2xl max-w-2xl w-full max-h-[90vh] overflow-y-auto">
        <div className="p-6">
          {/* Header */}
          <div className="flex items-center justify-between mb-6">
            <h2 className="text-xl font-bold text-blue-900">Review Permintaan</h2>
            <button 
              onClick={onClose}
              className="text-gray-500 hover:text-gray-700 text-2xl font-bold"
            >
              ×
            </button>
          </div>

          {/* Request Details */}
          <div className="bg-blue-50 rounded-lg p-4 mb-6 border border-blue-200">
            <h3 className="font-semibold text-blue-900 mb-3">Detail Permintaan</h3>
            <div className="grid grid-cols-1 md:grid-cols-2 gap-4 text-sm">
              <div>
                <p className="text-blue-600 font-medium">Nama Peminjam:</p>
                <p className="text-blue-900">{request.name}</p>
              </div>
              <div>
                <p className="text-blue-600 font-medium">Barang/Kendaraan:</p>
                <p className="text-blue-900">{request.item}</p>
              </div>
              <div>
                <p className="text-blue-600 font-medium">Tanggal Peminjaman:</p>
                <p className="text-blue-900">{formatDate(request.date)}</p>
              </div>
              <div>
                <p className="text-blue-600 font-medium">Tanggal Pengajuan:</p>
                <p className="text-blue-900">{new Date(request.createdAt).toLocaleDateString('id-ID')}</p>
              </div>
            </div>
            <div className="mt-4">
              <p className="text-blue-600 font-medium mb-2">Keperluan Dinas:</p>
              <p className="text-blue-900 bg-white p-3 rounded border border-blue-200">{request.purpose}</p>
            </div>
          </div>

          {/* Action Selection */}
          <div className="mb-6">
            <h3 className="font-semibold text-blue-900 mb-3">Keputusan</h3>
            <div className="flex gap-4">
              <button
                type="button"
                onClick={() => setAction('approve')}
                className={`flex-1 py-3 px-4 rounded-lg border-2 transition-colors ${
                  action === 'approve'
                    ? 'border-green-500 bg-green-50 text-green-700'
                    : 'border-gray-300 bg-white text-gray-700 hover:border-green-300'
                }`}
              >
                ✓ Setujui Permintaan
              </button>
              <button
                type="button"
                onClick={() => setAction('reject')}
                className={`flex-1 py-3 px-4 rounded-lg border-2 transition-colors ${
                  action === 'reject'
                    ? 'border-red-500 bg-red-50 text-red-700'
                    : 'border-gray-300 bg-white text-gray-700 hover:border-red-300'
                }`}
              >
                ✗ Tolak Permintaan
              </button>
            </div>
          </div>

          {/* Reason Input */}
          {action && (
            <form onSubmit={handleSubmit}>
              <div className="mb-6">
                <label htmlFor="reason" className="block text-sm font-medium text-blue-900 mb-2">
                  Alasan {action === 'approve' ? 'Persetujuan' : 'Penolakan'} *
                </label>
                <textarea
                  id="reason"
                  value={reason}
                  onChange={(e) => setReason(e.target.value)}
                  placeholder={`Berikan alasan ${action === 'approve' ? 'persetujuan' : 'penolakan'} yang jelas...`}
                  rows={4}
                  className="w-full px-3 py-2 border border-blue-300 rounded-lg focus:outline-none focus:ring-2 focus:ring-blue-500 focus:border-transparent"
                  required
                />
                <p className="text-xs text-blue-600 mt-1">
                  Alasan ini akan dikirim ke peminjam sebagai notifikasi
                </p>
              </div>

              {/* Action Buttons */}
              <div className="flex gap-3">
                <button
                  type="button"
                  onClick={onClose}
                  className="flex-1 py-3 px-4 border border-gray-300 text-gray-700 rounded-lg hover:bg-gray-50 transition-colors"
                >
                  Batal
                </button>
                <button
                  type="submit"
                  disabled={!reason.trim() || isSubmitting}
                  className={`flex-1 py-3 px-4 rounded-lg text-white font-medium transition-colors ${
                    action === 'approve'
                      ? 'bg-green-600 hover:bg-green-700 disabled:bg-green-300'
                      : 'bg-red-600 hover:bg-red-700 disabled:bg-red-300'
                  }`}
                >
                  {isSubmitting ? 'Memproses...' : `Konfirmasi ${action === 'approve' ? 'Persetujuan' : 'Penolakan'}`}
                </button>
              </div>
            </form>
          )}

          {/* Approver Info */}
          <div className="mt-6 pt-4 border-t border-blue-200">
            <p className="text-xs text-blue-600">
              Diproses oleh: <span className="font-medium">{approverName}</span> • 
              {new Date().toLocaleDateString('id-ID', { 
                weekday: 'long', 
                year: 'numeric', 
                month: 'long', 
                day: 'numeric',
                hour: '2-digit',
                minute: '2-digit'
              })}
            </p>
          </div>
        </div>
      </div>
    </div>
  )
}
